
"use client";

import { useState, useEffect } from "react";
import { drivers as initialDrivers, conversations } from "@/lib/data";
import { DataTable } from "@/components/data-table/data-table";
import { columns } from "./columns";
import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { PlusCircle, MessageSquare } from "lucide-react";
import { CreateDriverDialog } from "@/components/create-driver-dialog";
import type { Driver, Conversation, Message } from "@/lib/types";
import { DriverDataTableToolbar } from "./toolbar";
import {
  ContextMenu,
  ContextMenuContent,
  ContextMenuItem,
  ContextMenuTrigger,
} from "@/components/ui/context-menu";
import { BroadcastMessageDialog } from "@/components/broadcast-message-dialog";
import { useToast } from "@/hooks/use-toast";

export default function DriversPage() {
  const { toast } = useToast();
  const [drivers, setDrivers] = useState<Driver[]>(initialDrivers);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isBroadcastDialogOpen, setIsBroadcastDialogOpen] = useState(false);
  const [selectedDriver, setSelectedDriver] = useState<Driver | null>(null);
  const [rowSelection, setRowSelection] = useState({});
  const [isAdminMode, setIsAdminMode] = useState(false);

  useEffect(() => {
    const checkAdminMode = () => {
      setIsAdminMode(localStorage.getItem('isAdminMode') === 'true');
    };
    checkAdminMode();
    window.addEventListener('storage', checkAdminMode);
    return () => window.removeEventListener('storage', checkAdminMode);
  }, []);

  const handleCreateNew = () => {
    if (!isAdminMode) {
        toast({
        title: "Action Disabled",
        description: "Please add new drivers through the 'Data' page or enable Admin Mode.",
        variant: "destructive"
        });
        return;
    }
    setSelectedDriver(null);
    setIsDialogOpen(true);
  };

  const handleEdit = (driver: Driver) => {
     if (!isAdminMode) {
        toast({
        title: "Action Disabled",
        description: "Please edit driver details through the 'Data' page or enable Admin Mode.",
        variant: "destructive"
        });
        return;
    }
    setSelectedDriver(driver);
    setIsDialogOpen(true);
  };

  const handleDelete = (driverId: string) => {
    if (!isAdminMode) {
        toast({
            title: "Action Disabled",
            description: "Please delete from the 'Data' page or enable Admin Mode.",
            variant: "destructive"
        });
        return;
    }
    if (!window.confirm("Are you sure you want to delete this driver? This action cannot be undone.")) {
      return;
    }
    setDrivers(prev => prev.filter(d => d.id !== driverId));
    toast({ title: "Driver Deleted" });
  };

  const handleDialogSubmit = (driverData: Driver) => {
    const isEditing = drivers.some(d => d.id === driverData.id);
    if (isEditing) {
        setDrivers(prev => prev.map(d => d.id === driverData.id ? driverData : d));
    } else {
        setDrivers(prev => [driverData, ...prev]);
    }
    setSelectedDriver(null);
  };

  const handleBroadcastSubmit = ({ subject, message }: { subject: string; message: string }) => {
    const selectedIndices = Object.keys(rowSelection);
    const selectedDrivers = selectedIndices.map(index => drivers[parseInt(index)]);

    const broadcastContent = `Broadcast: ${subject}\n\n${message}`;

    selectedDrivers.forEach(driver => {
        let conversation = conversations.find(c => c.contactName === driver.user.name);
        
        const newMessage: Message = {
          id: `msg-${Date.now()}-${driver.id}`,
          sender: 'me',
          content: broadcastContent,
          timestamp: new Date().toISOString(),
        };

        if (conversation) {
          conversation.messages.push(newMessage);
          conversation.lastMessage = broadcastContent;
          conversation.lastMessageTime = newMessage.timestamp;
          conversation.isUnread = false;
        } else {
          const newConversation: Conversation = {
            id: `conv-${Date.now()}-${driver.id}`,
            contactName: driver.user.name,
            contactType: 'Driver',
            lastMessage: broadcastContent,
            lastMessageTime: newMessage.timestamp,
            messages: [newMessage],
            isUnread: false,
          };
          conversations.unshift(newConversation);
        }
    });

    toast({
      title: "Broadcast Sent!",
      description: `Your message has been sent to ${selectedDrivers.length} driver(s).`,
    });
    setIsBroadcastDialogOpen(false);
    setRowSelection({});
  };
  
  const selectedRowCount = Object.keys(rowSelection).length;

  return (
    <div>
       <PageHeader 
        title="Driver Management" 
        description={isAdminMode ? "Admin Mode: Full access enabled." : "View and manage all drivers. To add or edit, use the Data page."}
        action={
          <Button onClick={handleCreateNew}>
            <PlusCircle className="mr-2 h-4 w-4" />
            Add Driver
          </Button>
        }
      />
      <ContextMenu>
        <ContextMenuTrigger>
          <DataTable 
            columns={columns({ onEdit: handleEdit, onDelete: handleDelete })} 
            data={drivers} 
            toolbar={DriverDataTableToolbar}
            rowSelection={rowSelection}
            onRowSelectionChange={setRowSelection}
          />
        </ContextMenuTrigger>
        <ContextMenuContent>
          <ContextMenuItem 
            onSelect={() => setIsBroadcastDialogOpen(true)}
            disabled={selectedRowCount === 0}
          >
            <MessageSquare className="mr-2 h-4 w-4" />
            Broadcast Message ({selectedRowCount} selected)
          </ContextMenuItem>
        </ContextMenuContent>
      </ContextMenu>

       <CreateDriverDialog 
        isOpen={isDialogOpen}
        onOpenChange={setIsDialogOpen}
        onDriverSubmit={handleDialogSubmit}
        driver={selectedDriver}
       />
       <BroadcastMessageDialog
        isOpen={isBroadcastDialogOpen}
        onOpenChange={setIsBroadcastDialogOpen}
        selectedVehiclesCount={selectedRowCount}
        onSubmit={handleBroadcastSubmit}
      />
    </div>
  );
}
